<template>
  <div>
    <nav class="navbar" role="navigation" aria-label="main navigation">
      <div class="navbar-brand">
        <a class="navbar-item">
          <img src="https://www.iotech.co.id/assets/img/customAppLogo.png">
        </a>
      </div>
    </nav>
    <h3 class="title is-3">Downtime Record</h3>

    <fieldset disabled>
    <div class="field">
      <label class="label">Datetime</label>
      <div class="control">
        <input
        class="input"
          type="text"
          placeholder="Datetime"
          v-model="DATETIME"
        />
      </div>
    </div>
 
    <div class="field">
      <label class="label">SKU</label>
      <!-- <p>SKU</p> -->
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="SKU"
          v-model="SKU"
        />
      </div>
    </div>

    <div class="field">
      <label class="label">Status</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Status"
          v-model="STATUS"
        />
      </div>
    </div>

    <div class="field">
      <label class="label">Location</label>
      <div class="control">
        <input
          class="input"
          type="text"
          placeholder="Location"
          v-model="LOCATION"
        />
      </div>
    </div>

    </fieldset>

    <div class="field">
  <label class="label">Alasan</label>
  <div class="control">
    <div class="select">
      <select v-model="REASON">
        <option>Operator Habis</option>
        <option>Material Habis</option>
        <option>Emergency Stop</option>
      </select>
    </div>
  </div>
</div>
    

  <!-- <div class="control">
    <button class="button is-primary" @click="updateProduct">UPDATE</button>
    </div> -->

  <div class="field is-grouped">
  <p class="control">
    <a class="button is-primary" v-on:click="updateProduct">
      Submit
    </a>
  </p>
  <p class="control">
    <a class="button is-light" v-on:click=$router.back()>
      Cancel
    </a>
  </p>
</div>
  



</div>
</template>
 
<script>
// import axios
import axios from "axios";
 
export default {
  name: "EditProduct",
  data() {
    return {
      DATETIME: "",
      SKU: "",
      STATUS: "",
      REASON: "",
      LOCATION: ""
    };
  },
  created: function () {
    this.getDowntimebyId();
  },
  methods: {
    // Get Product By Id
    async getDowntimebyId() {
      try {
        const response = await axios.get(
        `http://192.168.0.93:5000/downtimes/${this.$route.params.id}`
        );
        this.DATETIME = response.data.Datetime;
        this.SKU = response.data.SKU;
        this.STATUS = response.data.Status;
        this.REASON = response.data.Reason;
        this.LOCATION = response.data.DowntimeLoc;
      } catch (err) {
        console.log(err);
      }
    },
 
    // Update product
    async updateProduct() {
      try {
        await axios.put(
          `http://192.168.0.93:5000/downtimes/${this.$route.params.id}`,
          {
            // product_name: this.productName,
            // product_price: this.productPrice,
            reason: this.REASON
          }
        );
        this.$router.push("/");
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>
 
<style>
</style>